.centered {
    text-align: center;
}

.display {
    margin-top: 50rem !important;
}

.ratio {
    font-size: 500%;
}

.percentage {
    font-size: 400%;
    margin-top: 5rem;
    position: relative;
}

.ui.input {
    font-size: initial;
    display: block;
}

.addition {
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
}

.addition-deaths {
    margin-top: 2rem;
}

.add {
    margin-top: 3rem !important;
}

.stats {
    font-size: initial;
    margin-top: 2rem;
}

.stats p {
    margin-bottom: 0;
}

.max-height {
    height: 100%;
}

#root {
    height: 100%;
}

.dimmed.dimmable > .ui.animating.dimmer, .dimmed.dimmable > .ui.visible.dimmer, .ui.active.dimmer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.ui.modal,
.ui.active.modal {
    margin: 0 auto!important;
    top: auto !important;
    left: auto !important;
    transform-origin: center !important;
    transition: all ease .5s;
}

.green {
    color: limegreen;
}

.red {
    color: red;
}

